.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-center {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.App-link {
	color: #61dafb;
}

.App-sorte {
	width: 30px;
}

.App-jokers {
	width: 40px;
}

.App-controls {
	position: fixed;
	top: 25px;
	right: 15px;
	display: flex;
	align-items: flex-end;
	flex-direction: column;
}

.Switch-container {
	display: flex;
	align-items: center;
}
.Switch-label {
	margin-right: 8px;
	color: white;
}

.control {
	margin-bottom: 10px;
}

.App-joueur-name {
	margin-bottom: "0px";
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}
